import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from "../views/login.vue"
import Forgotpass from "../views/forgotpassword.vue"
import Infor from "../views/information.vue"
import Register from "../views/register.vue"
import Security from "../views/security.vue"


Vue.use(VueRouter)

const routes = [
  {path:"/dang-nhap",name:"login",component:Login},
  {path:"/quen-mat-khau",name:"forgotpassword",component:Forgotpass},
  {path:"/dang-ki",name:"register",component:Register},
  {path:"/thong-tin-ca-nhan",name:"information",component:Infor},
  {path:"/cap-nhat-thong-tin",name:"security",component:Security},
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
